@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
}


.fbIcon{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 15px;
    padding: 0;
}
.fb{
    color: rgb(55, 55, 241);
    font-weight: 900;
    font-size: 20px;
    font-family: 'Quicksand', sans-serif;
}
.messeage{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.searchicon{
    padding-right: 6px;
    transform: translateY(2px);
}

.header_spans{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-top: 12px;
}
.header_spans span{
    cursor: pointer;
    color: rgb(87, 84, 84);
}
main{
    color: rgb(87, 84, 84);;
}
.header_spans span:hover{
    color: rgb(65, 65, 247);
    border-bottom: 2px solid rgb(65, 65, 247);
    border-radius: 1px;
}
.header_underline{
    width: 100%;
    height: 100%;
    border: 1px solid gray;
    opacity: 0.3;
    margin-bottom: 5px;
    transform: translateY(-1px);
}

.inputSection{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.profilelogo{
    margin-left: 10px;
    margin-right: 8px;
    color: rgb(187, 61, 145);
    transform: translateY(3px);
}
.inputSection input{
    border: 1px solid rgb(139, 138, 138);
    outline: none;
    border-radius: 25px;
    width: 100%;
    padding-left: 10px;
    padding-top: 4px;
    padding-bottom: 4px;
    margin-right: 10px;
    cursor: pointer;
}
.inputunderline{
    width: 100%;
    height: 100%;
    border: 1px solid gray;
    opacity: 0.3;
    transform: translateY(7px);
}
.videoSection{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 13px;
    cursor: pointer;
}
.videoSection p{
    font-size: 13px;
    font-weight: 700;
    color: rgb(59, 58, 58);
}
.line{
    opacity: 0.6;
}
.livephotoroom,
.livephotoroom1,
.livephotoroom2
{

    transform: translateY(3px) translateX(15px);
}
.livephotoroom{
    color: rgb(241, 43, 43);
}
.livephotoroom1{
    color: rgb(79, 180, 53);
}
.livephotoroom2{
    font-size: 19px;
    color: rgb(116, 24, 119);
}

.videounderline{
    width: 100%;
    height: 100%;
    border: 3px solid gray;
    opacity: 0.3;
    transform: translateY(7px);
}

.carouselwrapper{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 10px;
    width: 100%;
    height: 100%;
}
.carousel{
   width: 65px;
   height: 93px;
   border: 1px solid rgb(172, 169, 169);
   display: flex;
   align-items: center;
   flex-direction: column;
   border-radius: 10px;
   margin: 5px;
}
.carousel img{
    margin-top:5px;
    width: 50%;
    height: 40%;
    border-radius: 50%;
    margin-bottom: 5px;
}
.carousel p{
    font-weight: 600;
    font-size: 13px;
    margin-bottom: 5px;
}
.carousel span{
    border: 1px solid rgb(65, 167, 201);
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(87, 87, 238);
    font-size: 11px;
    font-weight: 900px;
    padding-top: 2px;
    padding-bottom: 3px;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
}
.carousel span:hover{
    background-color: rgb(85, 131, 231);
    color: white;
    font-weight: 1000;
}
.carouselunderline{
    width: 100%;
    height: 100%;
    border: 3px solid gray;
    opacity: 0.3;
    margin-top: 2px;
}




