.postwrapper{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.imgpost{
    width: 65px;
    height: 93px;
    display: flex;
    margin-top: 10px;
    margin-left: 10px;
}
.postwrapper img{
    width: 50%;
    height: 35%;
    border-radius: 50%;
}
.postpeople{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   transform: translateY(-23px) translateX(-18px);
}
.postpeople .rony{
    font-size: 14px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(43, 42, 42);
}
.postpeople .time{
    font-size: 11px;
    font-family: sans-serif;
    color: rgb(43, 42, 42);
}
.timepeople{
    display: flex;
    font-size: 10px;
    transform: translateX(-38px);
}
.twopeople{
    font-size: 16px;
    transform: translateY(1px) translateX(5px);
}
.pong{
    width: 98%;
    height: 98%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;

}
.pimg p{
    font-size: 13px;
    font-family: sans-serif;
    color: rgb(51, 50, 50);
    transform: translateY(-41px);
    margin-left: 5px;
    margin-right: 5px;
}
.pimg img{
    width: 100%;
    height: 100%;
    transform: translateY(-33px);
}
.likewrapper{
    display: flex;
    align-items: center;
    justify-content: space-around;
    transform: translateY(-25px);
}
.comwrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-3px);
}
.comwrapper .like{
    font-size: 20px;
    cursor: pointer;
}
.comwrapper .redlike{
    color: rgb(187, 4, 4);
    font-size: 20px;
}
.comwrapper p{
    margin-left: 5px;
    font-size: 14px;
    transform: translateY(-3px);
    cursor: pointer;
}
.comwrapper span{
    cursor: pointer;
}
.likewrapperunderline{
    width: 100%;
    height: 100%;
    border: 1px solid gray;
    opacity: 0.3;
    transform: translateY(-32px);
    margin-bottom: 5px;
}
.likewrapperunderlineone{
    width: 100%;
    height: 100%;
    border: 3px solid gray;
    opacity: 0.3;
    transform: translateY(-22px);
}
.likefooter{
    transform: translateY(-19px);
    font-size: 12px;
    font-family: sans-serif;
}
